import React, { useState } from 'react';
import { GripVertical } from 'lucide-react';

interface CompressionComparisonProps {
    originalUrl: string;
    compressedUrl: string;
    originalSize: number;
    compressedSize: number;
    onClose: () => void;
}

export const CompressionComparison: React.FC<CompressionComparisonProps> = ({
    originalUrl,
    compressedUrl,
    originalSize,
    compressedSize,
    onClose
}) => {
    const [sliderPosition, setSliderPosition] = useState(50);
    const ratio = Math.round((compressedSize / originalSize) * 100);
    const saving = 100 - ratio;

    const handleDrag = (e: React.MouseEvent | React.TouchEvent) => {
        // 
        //  Drag container ref  offset
        //  Input Range 
    };

    const formatSize = (bytes: number) => {
        if (bytes === 0) return '0 B';
        const k = 1024;
        const sizes = ['B', 'KB', 'MB'];
        const i = Math.floor(Math.log(bytes) / Math.log(k));
        return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    };

    return (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/80 p-4" onClick={onClose}>
            <div
                className="relative w-full max-w-4xl overflow-hidden rounded-lg bg-gray-900 shadow-2xl"
                onClick={e => e.stopPropagation()}
            >
                <div className="flex items-center justify-between bg-gray-800 px-4 py-3 text-white">
                    <h3 className="font-bold"></h3>
                    <div className="space-x-4 text-sm">
                        <span className="text-gray-400">: <span className="text-white">{formatSize(originalSize)}</span></span>
                        <span className="text-emerald-400">: <span className="text-white">{formatSize(compressedSize)}</span> (-{saving}%)</span>
                        <button onClick={onClose} className="rounded bg-white/10 px-2 py-1 hover:bg-white/20"></button>
                    </div>
                </div>

                <div className="relative aspect-video w-full select-none overflow-hidden bg-black/50">
                    {/*  (Right/After) */}
                    <img
                        src={compressedUrl}
                        alt="Compressed"
                        className="absolute inset-0 h-full w-full object-contain"
                    />
                    {/*  Right */}
                    <div className="absolute right-4 top-4 rounded bg-black/50 px-2 py-1 text-xs text-white">After ({ratio}%)</div>

                    {/*  (Left/Before) -  clip-path  */}
                    <div
                        className="absolute inset-0 overflow-hidden"
                        style={{ clipPath: `inset(0 ${100 - sliderPosition}% 0 0)` }}
                    >
                        <img
                            src={originalUrl}
                            alt="Original"
                            className="absolute inset-0 h-full w-full object-contain"
                        />
                        {/*  Left */}
                        <div className="absolute left-4 top-4 rounded bg-black/50 px-2 py-1 text-xs text-white">Before (100%)</div>
                    </div>

                    {/* Slider Handle */}
                    <div
                        className="absolute inset-y-0 w-1 cursor-ew-resize bg-white shadow-[0_0_10px_rgba(0,0,0,0.5)]"
                        style={{ left: `${sliderPosition}%` }}
                    >
                        <div className="absolute top-1/2 -ml-3 flex h-8 w-8 -translate-y-1/2 items-center justify-center rounded-full bg-white text-gray-800 shadow-lg">
                            <GripVertical size={16} />
                        </div>
                    </div>

                    {/* Control Input (Invisible but interactive) */}
                    <input
                        type="range"
                        min="0"
                        max="100"
                        value={sliderPosition}
                        onChange={(e) => setSliderPosition(Number(e.target.value))}
                        className="absolute inset-0 z-10 h-full w-full cursor-ew-resize opacity-0"
                    />
                </div>

                <div className="bg-gray-800 p-2 text-center text-xs text-gray-400">
                    
                </div>
            </div>
        </div>
    );
};
